import { useTranslation } from "react-i18next";
import CustomSelectAcademicSubjectMaster from "../../../Ui/Select/CustomSelectAcademicSubjectMaster";
import { useClassAllMapSubjectMasterList } from "../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";

const InstituteAcademicSubjectMasterDropdown = ({
  did,
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
}) => {
  const { t } = useTranslation();

  const { classAllMapSubjectMasterList } = useClassAllMapSubjectMasterList({
    data: {
      did: did,
    },
    skip: !did,
  });
  return (
    <>
      <CustomSelectAcademicSubjectMaster
        selectLabel={t("select_subject_master")}
        selectedValue={t("please_select_subject_master")}
        options={
          classAllMapSubjectMasterList?.length > 0
            ? classAllMapSubjectMasterList
            : []
        }
        onClick={onSelect}
        isFull
        errorShow={errorShow}
        defalutValue={defalutValue}
        isDenied={isDenied}
        customStyleContainer={customStyleContainer}
      />
    </>
  );
};

export default InstituteAcademicSubjectMasterDropdown;
