import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import {
  useAcademicClassDseStudentExport,
  useClassMasterAllDseStudent,
} from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import InstituteDepartmentClassDseStudentCard from "./InstituteDepartmentClassDseStudentCard";
import { useLocation } from "react-router-dom";
import { cdnUrl } from "../../../../Services/UrlConfig/BaseUrl";
const InstituteDepartmentClassDseStudent = ({ clsId, openAs }) => {
  const getQuery = useLocation();
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [disabled, setDisabled] = useState(false);
  const [academicClassDseStudentExport] = useAcademicClassDseStudentExport();

  const { classMasterAllDseStudent, classMasterAllDseStudentLoading } =
    useClassMasterAllDseStudent({
      data: {
        cid: clsId,
        page: page,
        limit: 10,
        search: search,
        did: getQuery.state?.departmentId,
      },
      skip: !clsId,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (classMasterAllDseStudent?.list?.length) {
      let obj = nextPage(classMasterAllDseStudent?.list?.length);
      setState(() => obj);
    }
  }, [classMasterAllDseStudent?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onExportData = () => {
    if (clsId && getQuery.state?.departmentId) {
      setDisabled((pre) => !pre);
      academicClassDseStudentExport({
        did: getQuery.state?.departmentId,
        cid: clsId,
      })
        .then((res) => {
          if (res?.data?.set) {
            setDisabled((pre) => !pre);
            const newWindow = window.open(
              `${cdnUrl}/${res?.data?.set}`,
              "_blank",
              "noopener,noreferrer"
            );
            if (newWindow) newWindow.opener = null;
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <h6
            className={style.fm_title_heading}
            style={{
              width: "-webkit-fill-available",
            }}
          >
            {t("total_student")}

            {classMasterAllDseStudent?.studentCount ?? 0}
          </h6>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
          <img
            src={`${assestsUserFinanceUrl}/export.svg`}
            alt="icon"
            title="List Export"
            onClick={onExportData}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.3rem",
          paddingBottom: "1rem",
        }}
      >
        {classMasterAllDseStudent?.list?.map((student, index) =>
          classMasterAllDseStudent?.list?.length === index + 1 ? (
            <div key={student?._id} ref={ref}>
              <InstituteDepartmentClassDseStudentCard
                student={student}
                openAs={openAs}
              />
            </div>
          ) : (
            <InstituteDepartmentClassDseStudentCard
              student={student}
              key={student?._id}
              openAs={openAs}
            />
          )
        )}
        {classMasterAllDseStudentLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          classMasterAllDseStudent?.list?.length === 0 && (
            <EmptyMessage title="No student found related this search." />
          )
        ) : (
          classMasterAllDseStudent?.list?.length === 0 && (
            <EmptyMessage title="No student." />
          )
        )}
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default InstituteDepartmentClassDseStudent;
