import { useState } from "react";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import {
  useRemoveTheoryPracticalClasses,
  useUpdateTheoryPracticalClasses,
} from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import AddInstituteDepartmentPracticalBatchClass from "./AddInstituteDepartmentPracticalBatchClass";

const InstituteDepartmentPracticalBatchClassCardMenu = ({
  onClose,
  menuData,
  onRefetch,
  academicDepartmentId,
  clsId,
  instituteId,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);

  const [removeTheoryPracticalClasses] = useRemoveTheoryPracticalClasses();
  const [updateTheoryPracticalClasses] = useUpdateTheoryPracticalClasses();

  const onDelete = () => {
    if (menuData?._id && clsId) {
      setDisabled((pre) => !pre);
      removeTheoryPracticalClasses({
        sid: menuData?._id,
        bodyParameter: {
          flow: "PRACTICAL_BATCH",
          cid: clsId,
        },
      }).then(() => {
        onRefetch();
        setDisabled((pre) => !pre);
        onClose();
      });
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      onOpenEdit();
      updateTheoryPracticalClasses({
        sid: menuData?._id,
        bodyParameter: val,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      {edit ? (
        <AddInstituteDepartmentPracticalBatchClass
          onClose={onOpenEdit}
          menuData={menuData}
          onEdit={onEdit}
          openAs="EDIT"
          academicDepartmentId={academicDepartmentId}
          instituteId={instituteId}
        />
      ) : (
        <>
          <ModalMenu onClose={onClose} disabled={disabled}>
            <h6 onClick={onOpenEdit}>{t("edit")}</h6>
            <h6 onClick={onDelete}>{t("delete")}</h6>
          </ModalMenu>
        </>
      )}
    </>
  );
};

export default InstituteDepartmentPracticalBatchClassCardMenu;
