import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../../InstituteFeedback/InstituteFeedback.module.css";
import {
  assestsAcademicDepartmentUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";
import InstituteDepartmentPracticalBatchClassCardMenu from "./InstituteDepartmentPracticalBatchClassCardMenu";

const InstituteDepartmentPracticalBatchClassCard = ({
  subject,
  openAs,
  onRefetch,
  clsId,
  academicDepartmentId,
  instituteId,
}) => {
  const params = useParams();
  const getQuery = useLocation();
  const [open, setOpen] = useState(false);
  const onToggle = (e) => {
    if (e) e.preventDefault();
    setOpen((pre) => !pre);
  };
  return (
    <>
      <Link
        to={
          openAs === "DEPARTMENT_HEAD"
            ? `/q/${params.username}/member/staff/academic/department/${params.name}?type=practical-detail`
            : `/q/${params.username}/institute/member/academic/department/detail?type=cls-detail&n=practical-detail`
        }
        state={{
          ...getQuery.state,
          subId: subject?.subject?._id,
          subjectName: subject?.subject?.subjectName,
        }}
      >
        <div className={style.iadcc_card_hover}>
          <div className={style.sf_card_contatiner}>
            <img
              src={`${assestsAcademicDepartmentUrl}/cls-icon.svg`}
              loading="lazy"
              alt="avatar"
              style={{
                width: "2rem",
                height: "2rem",
                borderRadius: "0",
              }}
            />

            <div className={style.sf_card_contatiner_inner}>
              <h6
                style={{
                  marginBottom: "0",
                }}
              >
                {subject?.subject?.subjectName ?? ""}
              </h6>
              <p>
                {`${
                  subject?.subject?.subjectTeacherName?.staffFirstName ?? ""
                } ${
                  subject?.subject?.subjectTeacherName?.staffMiddleName ?? ""
                } ${subject?.subject?.subjectTeacherName?.staffLastName ?? ""}`}
              </p>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onToggle}
          />
        </div>
      </Link>
      {open && (
        <InstituteDepartmentPracticalBatchClassCardMenu
          onClose={onToggle}
          menuData={subject?.subject}
          onRefetch={onRefetch}
          clsId={clsId}
          academicDepartmentId={academicDepartmentId}
          instituteId={instituteId}
        />
      )}
    </>
  );
};

export default InstituteDepartmentPracticalBatchClassCard;
