import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { useGetAllTheoryClasses } from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import CreateButton from "../../../../Ui/Button/CreateButton";
import InstituteDepartmentTheoryClassCard from "./InstituteDepartmentTheoryClassCard";
import AddInstituteDepartmentTheoryClass from "./AddInstituteDepartmentTheoryClass";
import { useLocation } from "react-router-dom";
const InstituteDepartmentTheoryClass = ({ clsId, instituteId, openAs }) => {
  const getQuery = useLocation();
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);

  const {
    getAllTheoryClasses,
    getAllTheoryClassesLoading,
    getAllTheoryClassesRefetch,
  } = useGetAllTheoryClasses({
    data: {
      cid: clsId,
      page: page,
      limit: 10,
      search: search,
      did: getQuery.state?.departmentId,
    },
    skip: !clsId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (getAllTheoryClasses?.list?.length) {
      let obj = nextPage(getAllTheoryClasses?.list?.length);
      setState(() => obj);
    }
  }, [getAllTheoryClasses?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    getAllTheoryClassesRefetch();
  }, [getAllTheoryClassesRefetch]);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
          <CreateButton
            label={t("add")}
            customStyle={{
              margin: "0",
              padding: "0.4rem 1rem",
              minWidth: "fit-content",
            }}
            onAction={onToggle}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.3rem",
          paddingBottom: "1rem",
        }}
      >
        {getAllTheoryClasses?.list?.map((subject, index) =>
          getAllTheoryClasses?.list?.length === index + 1 ? (
            <div key={subject?._id} ref={ref}>
              <InstituteDepartmentTheoryClassCard
                subject={subject}
                openAs={openAs}
                clsId={clsId}
                onRefetch={onRefetch}
                academicDepartmentId={getQuery.state?.departmentId}
                instituteId={instituteId}
              />
            </div>
          ) : (
            <InstituteDepartmentTheoryClassCard
              subject={subject}
              key={subject?._id}
              openAs={openAs}
              clsId={clsId}
              onRefetch={onRefetch}
              academicDepartmentId={getQuery.state?.departmentId}
              instituteId={instituteId}
            />
          )
        )}
        {getAllTheoryClassesLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          getAllTheoryClasses?.list?.length === 0 && (
            <EmptyMessage title="No subject found related this search." />
          )
        ) : (
          getAllTheoryClasses?.list?.length === 0 && (
            <EmptyMessage title="No subject." />
          )
        )}
      </UserMemberContentWrapper>
      {add && (
        <AddInstituteDepartmentTheoryClass
          onClose={onToggle}
          clsId={clsId}
          onRefetch={onRefetch}
          instituteId={instituteId}
          academicDepartmentId={getQuery.state?.departmentId}
        />
      )}
    </>
  );
};

export default InstituteDepartmentTheoryClass;
